import { createWebHistory, createRouter } from "vue-router";
import ContentMain from './components/ContentMain';
import SystemGroup from './components/systemsetting/SystemGroup';
import SystemUser from './components/systemsetting/SystemUser';
import SystemPermission from './components/systemsetting/SystemPermission';
import SystemTenant from './components/systemsetting/SystemTenant';
import SystemDiscount from './components/systemsetting/SystemDiscount';
import SystemParking from './components/systemsetting/SystemParking';
import SystemOperation from './components/systemsetting/SystemOperation';
import SystemHolyday from './components/systemsetting/SystemHolyday';
import SystemDevice from './components/systemsetting/SystemDevice';
import SystemRate from './components/systemsetting/SystemRate';

import ParkingList from './components/parkingmanagement/ParkingList';
import ParkingEntex from './components/parkingmanagement/ParkingEntex';
import ParkingAlarm from './components/parkingmanagement/ParkingAlarm';
import ParkingLimitedCar from './components/parkingmanagement/ParkingLimitedCar';
import ParkingUnpaidCar from './components/parkingmanagement/ParkingUnpaidCar';

import TicketCar from './components/ticketmanagement/TicketCar';
import TicketGroup from './components/ticketmanagement/TicketGroup';
import TicketPurchase from './components/ticketmanagement/TicketPurchase';

import DiscountApply from './components/discountmanagement/DiscountApply';
import DiscountCompany from './components/discountmanagement/DiscountCompany';
import DiscountPurchase from './components/discountmanagement/DiscountPurchase';


import TotalList from './components/total/TotalList';
import TotalStatus from './components/total/TotalStatus';
import TotalDiscount from './components/total/TotalDiscount';
import TotalDiscountStatus from './components/total/TotalDiscountStatus';
import TotalEntryExit from './components/total/TotalEntryExit';
import TotalEntryExitSatus from './components/total/TotalEntryExitSatus';
import TotalCardCompanyList from './components/total/TotalCardCompanyList';
import TotalCardCompanyStatus from './components/total/TotalCardCompanyStatus';




import TenantDiscount from './components/tenantmanagement/TenantDiscount';
import TenantTicket from './components/tenantmanagement/TenantTicket';

import Login from './components/login/Login';


const routes = [
  {
    path: "/",
    component: ContentMain,
  },
  {
    path: "/Login",
    component: Login,
  },
  {
    path: "/SystemGroup",
    component: SystemGroup,
  },
  {
    path: "/SystemUser",
    component: SystemUser,
  },
  {
    path: "/SystemPermission",
    component: SystemPermission,
  },
  {
    path: "/SystemTenant",
    component: SystemTenant,
  },
  {
    path: "/SystemDiscount",
    component: SystemDiscount,
  },
  {
    path: "/SystemParking",
    component: SystemParking,
  },
  {
    path: "/SystemOperation",
    component: SystemOperation,
  },
  {
    path: "/SystemHolyday",
    component: SystemHolyday,
  },
  {
    path: "/SystemDevice",
    component: SystemDevice,
  },
  {
    path: "/SystemRate",
    component: SystemRate,
  },
  {
    path: "/ParkingList",
    component: ParkingList,
  },
  {
    path: "/ParkingEntex",
    component: ParkingEntex,
  },
  {
    path: "/ParkingAlarm",
    component: ParkingAlarm,
  },
  {
    path: "/ParkingLimitedCar",
    component: ParkingLimitedCar,
  },
  {
    path: "/ParkingUnpaidCar",
    component: ParkingUnpaidCar,
  },
  {
    path: "/TicketCar",
    component: TicketCar,
  },
  {
    path: "/TicketGroup",
    component: TicketGroup,
  },
  {
    path: "/TicketPurchase",
    component: TicketPurchase,
  },
  {
    path: "/DiscountApply",
    component: DiscountApply,
  },
  {
    path: "/DiscountCompany",
    component: DiscountCompany,
  },
  {
    path: "/DiscountPurchase",
    component: DiscountPurchase,
  },
  {
    path: "/TenantDiscount",
    component: TenantDiscount,
  },
  {
    path: "/TenantTicket",
    component: TenantTicket,
  },


  {
    path: "/TotalList",
    component: TotalList,
  },
  {
    path: "/TotalStatus",
    component: TotalStatus,
  },
  {
    path: "/TotalDiscount",
    component: TotalDiscount,
  },
  {
    path: "/TotalDiscountStatus",
    component: TotalDiscountStatus,
  },
  {
    path: "/TotalEntryExit",
    component: TotalEntryExit,
  },
  {
    path: "/TotalEntryExitSatus",
    component: TotalEntryExitSatus,
  },
  {
    path: "/TotalCardCompanyList",
    component: TotalCardCompanyList,
  },
  {
    path: "/TotalCardCompanyStatus",
    component: TotalCardCompanyStatus,
  },

];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 